import React, { useState } from "react";
import {
  Box,
  Typography,
  TextField,
  Button,
  // Select,
  // MenuItem,
  Grid,
  IconButton,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import RefreshIcon from "@mui/icons-material/Refresh";

const ImproveText = () => {
  // const [language, setLanguage] = useState("");
  // const [wordCount, setWordCount] = useState(" ");
  const [history, setHistory] = useState([]);
  const [selectedTextDetail, setSelectedTextDetail] = useState(null);
  const [inputText, setInputText] = useState("");

  //           
  const generateText = () => {
    if (inputText.trim() === "") return;

    const newEntry = {
      text: inputText.slice(0, 50) + (inputText.length > 50 ? "..." : ""),
      fullText: inputText,
      date: new Date().toLocaleDateString(),
    };

    //        
    setHistory([...history, newEntry]);
    setSelectedTextDetail(newEntry);
    setInputText(""); //      
  };

  //      
  const handleDelete = (index) => {
    setHistory(history.filter((_, i) => i !== index));
  };

  //      
  const handleOpen = (entry) => {
    setSelectedTextDetail(entry);
  };

  //      
  const handleBack = () => {
    setSelectedTextDetail(null);
  };

  return (
    <Box
      sx={{
        p: 4,
      }}
    >
      {selectedTextDetail ? (
        //   
        <Box>
          <Box display="flex" alignItems="center" mb={2}>
            <Button
              startIcon={<ArrowBackIcon />}
              onClick={handleBack}
              sx={{
                color: "#8e44ad",
                textTransform: "none",
                fontSize: "0.875rem",
              }}
            >
              
            </Button>
          </Box>

          {/*    */}
          <Box
            sx={{
              p: 3,
              backgroundColor: "#f9f9f9",
              borderRadius: "16px",
              boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
              mb: 3,
            }}
          >
            <Typography variant="body1" sx={{ mb: 2 }}>
              {selectedTextDetail.fullText}
            </Typography>
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
            >
              <Typography variant="body2" color="textSecondary">
                {selectedTextDetail.date}
              </Typography>
              <IconButton
                onClick={() =>
                  navigator.clipboard.writeText(selectedTextDetail.fullText)
                }
              >
                <FileCopyIcon />
              </IconButton>
            </Box>
          </Box>
          <Box
            sx={{
              p: 3,
              mb: 3,
            }}
          >
            <Typography variant="body1" sx={{ mb: 2 }}>
              {selectedTextDetail.fullText}
            </Typography>
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
            >
              <Typography variant="body2" color="textSecondary">
                {selectedTextDetail.date}
              </Typography>
              <IconButton
                onClick={() =>
                  navigator.clipboard.writeText(selectedTextDetail.fullText)
                }
              >
                <FileCopyIcon />
              </IconButton>
            </Box>
          </Box>

          {/*   */}
          <Box display="flex" justifyContent="space-between" mt={10}>
            <Button
              fullWidth
              startIcon={<FileCopyIcon />}
              sx={{
                backgroundColor: "#f0f0f0",
                color: "#000",
                borderRadius: "16px",
                padding: "10px 16px",
                marginRight: 1,
                "&:hover": {
                  backgroundColor: "#e0e0e0",
                },
              }}
            >
              
            </Button>
            <Button
              fullWidth
              startIcon={<RefreshIcon />}
              sx={{
                backgroundColor: "#f0f0f0",
                color: "#000",
                borderRadius: "16px",
                padding: "10px 16px",
                marginLeft: 1,
                "&:hover": {
                  backgroundColor: "#e0e0e0",
                },
              }}
            >
              
            </Button>
          </Box>
        </Box>
      ) : (
        //  
        <>
          {/*  */}
          <Box textAlign="center" sx={{ mb: 4 }}>
            <Typography
              variant="h5"
              sx={{ mb: 1, fontWeight: 500, color: "#333" }}
            >
                   AI
            </Typography>
            <Typography variant="subtitle2" color="textSecondary">
                     .
            </Typography>
          </Box>

          {/*   */}
          <Box sx={{ mb: 3, borderRadius: "16px", overflow: "hidden" }}>
            <TextField
              fullWidth
              multiline
              rows={6}
              placeholder=" ..."
              variant="outlined"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              sx={{
                backgroundColor: "#fff",
                "& .MuiOutlinedInput-root": {
                  borderRadius: "16px",
                  "& fieldset": {
                    borderColor: "#ddd",
                  },
                  "&:hover fieldset": {
                    borderColor: "#8e44ad",
                  },
                  "&.Mui-focused fieldset": {
                    borderColor: "#8e44ad",
                  },
                },
              }}
            />
          </Box>

          {/*  */}
          <Grid container spacing={2} alignItems="center" sx={{ mb: 4 }}>
            <Grid item xs={-1}>
              {/* <Select
                fullWidth
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
                variant="outlined"
                sx={{
                  backgroundColor: "#fff",
                  borderRadius: "16px",
                  minWidth: "120px",
                }}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
              </Select> */}
            </Grid>
            <Grid item xs={4}>
              {/* <Select
                fullWidth
                value={wordCount}
                onChange={(e) => setWordCount(e.target.value)}
                variant="outlined"
                sx={{
                  backgroundColor: "#fff",
                  borderRadius: "16px",
                  minWidth: "120px",
                }}
              >
                <MenuItem value=" "> </MenuItem>
                <MenuItem value="100 ">100 </MenuItem>
                <MenuItem value="200 ">200 </MenuItem>
              </Select> */}
            </Grid>
            <Grid item xs={4}>
              <Button
                fullWidth
                variant="contained"
                sx={{
                  backgroundColor: "#6c63ff",
                  color: "#fff",
                  borderRadius: "16px",
                  padding: "10px 16px",
                  "&:hover": {
                    backgroundColor: "#5752d4",
                  },
                }}
                onClick={generateText}
              >
                
              </Button>
            </Grid>
          </Grid>

          {/*  */}
          <Typography variant="h6" sx={{ mb: 2 }}>
            
          </Typography>
          <Box
            sx={{
              p: 2,
              backgroundColor: "#fff",
              borderRadius: "16px",
              borderColor: "#ddd",
            }}
          >
            {history.map((entry, index) => (
              <Box
                key={index}
                display="flex"
                justifyContent="space-between"
                alignItems="center"
                sx={{ cursor: "pointer", mb: 1 }}
              >
                <Box onClick={() => handleOpen(entry)} sx={{ flexGrow: 1 }}>
                  <Typography variant="body1">{entry.text}</Typography>
                  <Typography variant="body2" color="textSecondary">
                    {entry.date}
                  </Typography>
                </Box>
                <IconButton onClick={() => handleDelete(index)}>
                  <DeleteIcon />
                </IconButton>
              </Box>
            ))}
          </Box>
        </>
      )}
    </Box>
  );
};

export default ImproveText;
